<template>
  <div class="register-form">
    <el-header class="header" height="45px">
      <h2 class="title"></h2>
      <span class="pull-right">
        <router-link class="link" to="/loginForm"></router-link>
      </span>
    </el-header>
    <div class="form">
      <el-form :model="userForm" status-icon :rules="rules" ref="userForm">
        <el-form-item prop="userAccount">
          <el-input v-model="userForm.userAccount" autocomplete="off" placeholder=""
                    prefix-icon="el-icon-message"></el-input>
        </el-form-item>
        <el-form-item prop="checkCode">
          <el-input v-model="userForm.checkCode" :disabled="codeRight" autocomplete="off" placeholder=""
                    prefix-icon="el-icon-link"></el-input>
          <el-button plain style="position: absolute;right: 0; width: 40%;top: 0;" v-preventReClick
                     @click="sendEmailCode('userForm',$event)">
          </el-button>
        </el-form-item>
        <el-form-item prop="passWord">
          <el-input type="password" auto-complete="new-password" show-password v-model="userForm.passWord"
                    autocomplete="off" placeholder="" prefix-icon="el-icon-lock"></el-input>
        </el-form-item>
        <el-form-item prop="checkPassWord">
          <el-input type="password" auto-complete="new-password" show-password v-model="userForm.checkPassWord"
                    autocomplete="off" placeholder="" prefix-icon="el-icon-lock"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" class="submit" v-preventReClick @click="submitForm('userForm')"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: "Register_Form",
  data() {
    let CheckEmail = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else {
        if (this.$tools.checkEmail(value)) {
          callback();
        }
        return callback(new Error(""));
      }
    };
    let CheckCode = async (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      }
      if (this.checkCodeErrorMessage !== null) {
        callback(new Error(this.checkCodeErrorMessage));
      } else {
        callback()
      }
    };
    let CheckPass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else {
        if (this.$tools.checkPass(value)) {
          callback();
        }
        return callback(new Error("6~18"));
      }
    };
    let CheckPass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else if (value !== this.userForm.passWord) {
        callback(new Error('!'));
      } else {
        callback();
      }
    };
    return {
      // 
      checkCodeErrorMessage: null,
      //
      codeButtonTemp: null,
      //URL
      checkCodeUrl: null,
      //
      rememberMe: false,
      //
      codeRight: true,
      //
      userForm: {
        userAccount: '',
        passWord: '',
        checkCode: '',
        checkPassWord: '',
      },
      //
      rules: {
        userAccount: [
          {validator: CheckEmail, trigger: 'blur'}
        ],
        checkCode: [
          {required: true, message: '', trigger: 'blur'}
        ],
        passWord: [
          {validator: CheckPass, trigger: 'blur'}
        ],
        checkPassWord: [
          {validator: CheckPass2, trigger: 'blur'}
        ],
      }
    }
  },
  methods: {
    //
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$http.post("/allow/reset-password?account=" + this.userForm.userAccount +
            "&password=" + this.userForm.passWord + "&authCode=" + this.userForm.checkCode).then((res) => {
            if (res.data.code === 200) {
              debugger;
              //
              this.$countDown.removeItem(this.codeButtonTemp);
              this.$msg.success({message: res.data.message, showClose: true, duration: 1500});
              this.$http.post("/allow/sendHtmlResetPwd?email=" + this.userForm.userAccount + "&password=" + this.userForm.passWord);
              this.$router.push('/loginForm');
            } else {
              //code 500
              this.$msg.warning({message: res.data.message, showClose: true, duration: 1500});
            }
          }).catch((err) => {  //
            this.$msg.error({message: '' + err, showClose: true, duration: 1500});
          })
        } else {
          return false;
        }
      });
    },
    //
    sendEmailCode(formName, event) {
      this.codeButtonTemp = event.currentTarget;
      let va = true;
      this.$refs[formName].validateField('userAccount', (valid) => {
        va = valid === "";
      })
      if (va) {
        let loading = this.$loading({lock: true, text: "", background: "rgba(255,255,255,0.1)"});
        this.$http.post("/allow/sendHtmlCode?email=" + this.userForm.userAccount).then((res) => {
          loading.close();
          this.codeRight = false;
          this.$countDown.setItem(this.codeButtonTemp);
          this.$message.success("");
        }).catch((err) => {
          this.checkCodeErrorMessage = err.message;
          this.checkCodeErrorMessage = null;
          loading.close();
        })
      }
    }
  },
  created() {
  }
}
</script>


<style>
.register-form .header {
  position: relative;
  text-align: left;
  border: none;
  padding: 0;
}

.register-form .header .title {
  margin: 0;
}

.register-form .header .pull-right {
  position: absolute;
  top: 15px;
  right: 20px;
  font-size: 14px;
}

.register-form .header .pull-right .link {
  text-decoration: none;
  cursor: pointer;
  color: #005980;
}

.register-form .form {
  text-align: left;
}

.register-form .form .check-code {
  position: absolute;
  right: 20px;
  cursor: pointer;
}


.register-form .form .submit {
  width: 100%;
}

.register-form .el-form-item {
  margin-bottom: 19px;
}
</style>



